import React from 'react';
import ReactDOM from 'react-dom';

export default class Slideshow extends React.Component {
    constructor(props){
        super(props)
        this.state = {stage:0}
    }
    render(){
        var array = ["enter","show","exit"]
        return(
            <div id="full-height full-width">
                <FancyImg slideStage = {array[(this.state.stage+1)%3]} src={"./images/wyattPic.jpg" } rel="preload"/>

                <FancyImg slideStage = {array[(this.state.stage+0)%3]} src={"./images/familyHonduras.jpg"}/>
                <FancyImg slideStage = {array[(this.state.stage+2)%3]} src={"./images/wildflowers.jpg"} rel="preload"/>
                <div id = {"titleContainer"}>
                    <h1 id = {"nameTitle"}>Wyatt Cowley <a id={"lowerArrow"} href={"#aboutMe"}><img src="./images/chevron.png" className = {"arrowThing"}></img></a></h1>
                </div>
            </div>
        )
    }
    switch(){
        this.setState({
            stage: (this.state.stage+1)
        })
    }
    componentDidMount() {
        this.intervalClearer = setInterval(() => this.switch(),8000)
    }
    // called when destroyed
    componentWillUnmount() {
        clearInterval(this.intervalClearer)
    }
}
class FancyImg extends React.Component {
    constructor(props){
        super(props)
    }

    render(){
        return(
            <img className = {" full-width full-height openerSlide "+this.props.slideStage} src={this.props.src}></img>
        )
    }
}